/* eslint-disable no-undef */
/* eslint-disable no-param-reassign */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
import { get } from 'http';

export default class AppView {
  constructor(model) {
    this.model = model;
    this.time = 300;
    this.resultWidth = 632;
    this.windowWidth = window.innerWidth
      || document.documentElement.clientWidth
      || document.body.clientWidth;
    this.resultsPerPage = (this.windowWidth
      - (this.windowWidth % this.resultWidth)) / this.resultWidth;
    this.clickPoint = 0;
    this.currentPosition = 0;
    this.previousPosition = 0;
    this.isMouseDown = false;
    this.isNearEndOfList = false;
    this.correctiveMove = 0;
    this.pages = [
      {
        number: 1,
        position: this.resultWidth * 0 * this.resultsPerPage,
        isSelected: true,
        isHidden: false,
      },
      {
        number: 2,
        position: this.resultWidth * 1 * this.resultsPerPage,
        isSelected: false,
        isHidden: false,
      },
    ];
  }

  render() {
    document.body.innerHTML = '<div class="search"> <input id="searchBox"  class="input"  type="search" name=""> <input id="searchButton" class="submit"  type="button"> </div> <div class="searchResultsWrapper"> <ul id="results" class="searchResultsList"></ul> </div> </div> <div id="pagesWrapper" class="pagesWrapper"> ';


    document.getElementById('searchButton').addEventListener('click', () => {
      this.onClickSerach(this);
    });

    document.getElementById('searchBox').addEventListener('keydown', (e) => {
      if (e.which === 13) {
        this.onClickSerach(this);
      }
    });

    document.getElementById('results').addEventListener('mousedown', (e) => {
      this.onMouseDown(e, this);
    });

    document.body.addEventListener('mouseup', (e) => {
      this.onMouseUp(e, this);
    });

    document.getElementById('results').addEventListener('mousemove', e => _.debounce(this.onMouseMove(e, this)), 30);
  }

  updatePages(model) {
    const pagesWrapper = document.getElementById('pagesWrapper');

    pagesWrapper.innerHTML = '';

    model.pages.filter(element => !element.isHidden)
      .sort((a, b) => b - a)
      .slice(0, 4)
      .sort((a, b) => a - b)
      .forEach((element) => {
        const div = document.createElement('div');
        div.innerHTML = element.number;
        element.isHidden = false;
        div.setAttribute('class', 'page');
        div.setAttribute('id', element.number);
        div.setAttribute('position', element.position);
        if (element.isSelected) div.style.backgroundColor = '#ffbfca';
        pagesWrapper.appendChild(div);
        div.addEventListener('click', (e) => {
          this.onClickPage(e, this);
        });
      });
  }

  updateResults(model) {
    const template = '<div class="clip button"> <a class="title" href="https://www.youtube.com/watch?v={{url}}" target="_blank">{{title}}</a> <div class="image" style="background-image: url({{img}})"></div> <div class="channel">Channel: {{channel}}</div> <div class="date">Uploading date: {{date}}</div>  <div class="views">Views count: {{views}}</div> <div class="description">{{description}}</div> </div>';
    const resultsWrapper = document.getElementById('results');

    model.searchResults.filter(element => !element.isExist).forEach((element) => {
      const li = document.createElement('li');
      li.innerHTML = template
        .replace(/{{img}}/g, element.img)
        .replace(/{{url}}/g, element.url)
        .replace(/{{title}}/g, element.title)
        .replace(/{{description}}/g, element.desc)
        .replace(/{{date}}/g, element.date)
        .replace(/{{channel}}/g, element.channel)
        .replace(/{{views}}/g, element.views);
      element.isExist = true;
      resultsWrapper.appendChild(li);
    });

    this.updatePages(this);
  }

  cleanResults(viewModel) {
    document.getElementById('results').innerHTML = '';
    document.getElementById('pagesWrapper').innerHTML = '';
    viewModel.pages.length = 2;
  }

  checkIfNewDataIsNeeded(viewModel) {
    if (viewModel.model.searchResults
      && (viewModel.model.searchResults.length - 6)
      * viewModel.resultWidth < viewModel.currentPosition
      * -1) {
      viewModel.isNearEndOfList = true;
    }
  }

  onClickSerach(viewModel) {
    viewModel.clickPoint = 0;
    viewModel.currentPosition = 0;
    viewModel.previousPosition = 0;
    viewModel.isMouseDown = false;
    viewModel.isNearEndOfList = false;
    viewModel.model.searchResults = [];
    viewModel.model.search(document.getElementById('searchBox').value);
  }

  updatePagesModels(viewModel, currentPageNumber) {
    if (!viewModel.model.searchResults.length) return;
    if (!_.find(viewModel.pages, x => x.number === currentPageNumber + 1)) {
      viewModel.pages.push(
        {
          number: currentPageNumber + 1,
          position: this.resultWidth * currentPageNumber * this.resultsPerPage,
          isSelected: false,
          isHidden: false,
        },
      );
    }
    viewModel.pages = viewModel.pages.map((page) => {
      page.isSelected = currentPageNumber === page.number;
      page.isHidden = currentPageNumber - 2 > page.number || currentPageNumber + 2 < page.number;
      page.position = (page.number - 1) * viewModel.resultWidth * viewModel.resultsPerPage;
      return page;
    });

    viewModel.updatePages(viewModel);
  }

  onMouseUp(element, viewModel) {
    viewModel.isMouseDown = false;
    viewModel.previousPosition = 0;

    let correctiveMove = viewModel.currentPosition * -1;

    if (viewModel.currentPosition < 0) {
      const reminder = Math.abs(viewModel.currentPosition % viewModel.resultWidth);
      correctiveMove = reminder > 632 / 2 ? -1 * (viewModel.resultWidth - reminder) : reminder;
    }

    const ticMove = correctiveMove / viewModel.time;
    const start = Date.now();
    const list = document.getElementById('results');
    function draw(timePassed) {
      list.style.left = `${viewModel.currentPosition + ticMove * timePassed}px`;
    }
    const timer = setInterval(() => {
      const timePassed = Date.now() - start;
      if (timePassed >= viewModel.time) {
        clearInterval(timer);
        viewModel.currentPosition += correctiveMove;
        const currentPage = viewModel.pages.find(x => x.position >= viewModel.currentPosition * -1);
        viewModel.updatePagesModels(viewModel, currentPage.number);
        return;
      }
      draw(timePassed);
    }, 20);
  }

  onMouseDown(element, viewModel) {
    viewModel.clickPoint = element.clientX;
    viewModel.isMouseDown = true;

    viewModel.checkIfNewDataIsNeeded(viewModel);
    if (viewModel.isNearEndOfList) {
      _.throttle(viewModel.model.expandSearchList, 2000)();
      viewModel.isNearEndOfList = false;
    }
  }

  onMouseMove(element, viewModel) {
    if (!viewModel.isMouseDown) return;
    viewModel.previousPosition = viewModel.previousPosition || viewModel.clickPoint;
    const move = element.clientX - viewModel.previousPosition;
    viewModel.previousPosition = element.clientX;
    viewModel.currentPosition += move;
    document.getElementById('results').style.left = `${viewModel.currentPosition}px`;
  }

  onClickPage(element, viewModel) {
    const currentPageNumber = +element.currentTarget.getAttribute('id');
    const position = +element.currentTarget.getAttribute('position') * -1;
    viewModel.currentPosition = position;

    viewModel.updatePagesModels(viewModel, currentPageNumber);

    viewModel.checkIfNewDataIsNeeded(viewModel);

    if (viewModel.isNearEndOfList) {
      viewModel.model.expandSearchList();
      viewModel.isNearEndOfList = false;
    }
  }
}
