import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

/* eslint-disable class-methods-use-this */
export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/search?',
      key: 'key=AIzaSyCkDPXTlI1pQkYbFU6qwQm3PHwLtZx-VWw',
      type: '&type=video&part=snippet&maxResults=15&order=viewCount&q=',
      stat_p_1: 'https://www.googleapis.com/youtube/v3/videos?',
      stat_p_2: '&id=',
      stat_p_3: '&part=snippet,statistics',
    };
    this.serachString = '';
    this.nextPageToken = '';
    this.currentPosition = 0;
  }

  async start() {
    const model = new AppModel(this.state);

    const viewModel = new AppView(model);

    model.searchResults = [];

    model.search = async (value) => {
      model.serachString = value;
      model.searchResults = [];
      viewModel.cleanResults(viewModel);
      await model.runSearch(
        model.state.url + model.state.key + model.state.type + model.serachString,
        model.state.stat_p_1 + model.state.key + model.state.stat_p_2,
        model.state.stat_p_3,
      );
    };

    model.runSearch = async (getIdQuery, getIdQueryPart1, getIdQueryPart2) => {
      await model.getClipData(getIdQuery, getIdQueryPart1, getIdQueryPart2)
        .then((response) => {
          this.nextPageToken = response.nextPageToken;
          model.searchResults = response.data;
          viewModel.updateResults(model);
        });
    };

    model.expandSearchList = async () => {
      const token = `&pageToken=${this.nextPageToken}`;
      await model.runSearchExpand(
        model.state.url + model.state.key + model.state.type + model.serachString + token,
        model.state.stat_p_1 + model.state.key + model.state.stat_p_2,
        model.state.stat_p_3,
      );
    };

    model.runSearchExpand = async (getIdQuery, getIdQueryPart1, getIdQueryPart2) => {
      await model.getClipData(getIdQuery, getIdQueryPart1, getIdQueryPart2)
        .then((response) => {
          this.nextPageToken = response.nextPageToken;
          model.searchResults = model.searchResults.concat(response.data);
          viewModel.updateResults(model);
        });
    };

    viewModel.render();
  }
}
