/* eslint-disable class-methods-use-this */
export default class AppModel {
  constructor(state) {
    this.state = {
      url: state.url,
      key: state.key,
      type: state.type,
      stat_p_1: state.stat_p_1,
      stat_p_2: state.stat_p_2,
      stat_p_3: state.stat_p_3,
    };

    this.serachString = '';
  }

  static extractClipNames(data) {
    return data.snippet.title;
  }

  static extractClipImages(data) {
    return data.snippet.thumbnails.high.url;
  }

  static extractClipUrls(data) {
    return data.id;
  }

  static extractClipViews(data) {
    return data.statistics.viewCount;
  }

  static extractClipChannel(data) {
    return data.snippet.channelTitle;
  }

  static extractClipDate(data) {
    return new Date(data.snippet.publishedAt).toDateString();
  }

  static extractClipDescription(data) {
    return data.snippet.description;
  }

  async getClipData(getIdQuery, getIdQueryPart1, getIdQueryPart2) {
    let responce = await fetch(getIdQuery);
    let data = await responce.json();
    const token = data.nextPageToken;
    if (!data || !data.items || data.items.lenght) {
      // eslint-disable-next-line no-alert
      alert('We have no result or error is happend!');
      return { nextPageToken: token, data: [] };
    }
    const ids = data.items.map(element => element.id.videoId).join(',');
    responce = await fetch(getIdQueryPart1 + ids + getIdQueryPart2);
    data = await responce.json();

    return {
      nextPageToken: token,
      data: data.items.map(element => (
        {
          title: AppModel.extractClipNames(element),
          img: AppModel.extractClipImages(element),
          url: AppModel.extractClipUrls(element),
          desc: AppModel.extractClipDescription(element),
          date: AppModel.extractClipDate(element),
          views: AppModel.extractClipViews(element),
          channel: AppModel.extractClipChannel(element),
        })),
    };
  }
}
